<template>
  <div>
    <v-toolbar flat class="primary">
      <v-toolbar-title class="text-uppercase font-weight-bold display-1">
         
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-list class="primary">
        <v-list-item>
          <v-list-item-content style="text-align: right">
            <v-list-item-title class="text-h6">
              {{ gatewayinfo.pid }}&nbsp; {{ gatewayinfo.alias }}
            </v-list-item-title>
            <v-list-item-subtitle
              v-text="
                gatewayinfo.connect_state === false ? ' ' : ''
              "
              > </v-list-item-subtitle
            >
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-toolbar>
    <div class="mx-4 mt-3">
      <v-divider></v-divider>
      <div class="mt-6">
        <v-row>
          <v-col cols="4">
            <v-card>
              <v-toolbar flat>
                <v-card-subtitle
                  class="black--text font-weight-bold mt-n2 ml-n4"
                >
                  
                </v-card-subtitle>
                <v-spacer></v-spacer>
                <v-btn
                  class="mt-n2 font-weight-medium"
                  text
                  color="point2"
                  depressed
                  >
                </v-btn>
              </v-toolbar>

              <v-list class="mt-n4">
                <v-list-item>
                  <v-list-item-content>
                    
                  </v-list-item-content>
                  <v-list-item-action>
                    {{ gatewayinfo.alias }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                     
                  </v-list-item-content>
                  <v-list-item-action>
                    {{ gatewayinfo.pid }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                     
                  </v-list-item-content>
                  <v-list-item-action>
                    {{ gatewayinfo.ip }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                     
                  </v-list-item-content>
                  <v-list-item-action>
                    Beacon Enabled
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                      
                  </v-list-item-content>
                  <v-list-item-action>
                    {{
                      $date.changeFullDateFormat(
                        new Date(gatewayinfo.connect_time) - $date.KR_TIME_DIFF
                      )
                    }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                       
                  </v-list-item-content>
                  <v-list-item-action>
                    {{
                      $date.changeFullDateFormat(
                        new Date(gatewayinfo.disconnect_time) -
                          $date.KR_TIME_DIFF
                      )
                    }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    
                  </v-list-item-content>
                  <v-list-item-action>
                    {{ $date.changeLocalDateFormat(gatewayinfo.created) }}
                  </v-list-item-action>
                </v-list-item>
              </v-list>
            </v-card></v-col
          >
          <v-col cols="8">
            <v-card>
              <v-toolbar class="mb-n4" flat>
                <v-toolbar-title class="subtitle-1 black--text font-weight-bold"
                  ></v-toolbar-title
                >

                <v-spacer></v-spacer>
                <v-btn depressed color="white">
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </v-toolbar>
              <v-data-table
                falt
                :headers="headers"
                :items="bandList"
                :items-per-page="5"
              >
                <template v-slot:item.actions="{ item }">
                  <v-icon small @click="deleteItem(item)">
                    mdi-delete
                  </v-icon>
                </template>
              </v-data-table>
            </v-card>
          </v-col>
        </v-row>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  async mounted() {
    this.gatewayinfo = JSON.parse(this.$route.query.gatewayinfo);
    this.bandList = await this.getBandList();
  },
  methods: {
    async getBandList() {
      var bandlist = null;
      try {
        var res = await this.$http.get(
          "gateways/bandinfo/" + this.gatewayinfo.id
        );
        bandlist = res.data.data;
      } catch (ex) {
        bandlist = null;
      }
      return bandlist;
    },
  },
  data() {
    return {
      restapi: false,
      gatewayinfo: { pid: "", alias: "" },
      headers: [
        {
          text: "",
          align: "start",
          sortable: true,
          value: "id",
        },
        { text: " ", value: "bid", sortable: true },
        { text: "", value: "alias", sortable: true },
        { text: "", value: "name", sortable: true },
        { text: "", value: "actions", sortable: false },
      ],
      bandList: [
        { id: 1, bid: "0x00", alias: "D1", name: "" },
        { id: 2, bid: "0x00", alias: "D1", name: "" },
        { id: 3, bid: "0x00", alias: "D1", name: "" },
      ],
    };
  },
};
</script>
