<template>
  <div v-if="loading">
    <div v-if="result != null">
      <div class="ml-4 title"></div>
      <v-row no-gutters class="">
        <v-col cols="5">
          <div class="text-center">
            <v-icon size="100">
              {{ changeIcon(result.status) }}
            </v-icon>
          </div>
        </v-col>
        <v-col cols="7">
          <v-row no-gutters>
            <v-col cols="6">
              <div class="display-2 text-right mr-4">
                {{ result.temp }}
              </div>
            </v-col>
            <v-col cols="6">
              <div class="title">{{ result.min }}/ {{ result.max }}</div>
            </v-col>
          </v-row>
          <v-row no-gutters class="mt-n4">
            <v-col cols="12">
              <div class="title text-right mr-2">
                {{ result.status }}
              </div>
            </v-col>
          </v-row>
          <v-row no-gutters>
            <v-col cols="12" class="text-right">
              <span class="subtitle-1">
                
              </span>
              <span
                class="subtitle-1"
                :class="finedustTextColor(result.finedust)"
              >
                {{ result.finedust }}
              </span>
              <span class="subtitle-1">
                 
              </span>
              <span
                class="subtitle-1 mr-2"
                :class="finedustTextColor(result.ultrafinedust)"
              >
                {{ result.ultrafinedust }}
              </span>
            </v-col>
          </v-row>
        </v-col></v-row
      >
      <v-row no-gutters>
        <v-col cols="12" align="center" justify="center">
          <v-row no-gutters class=" ">
            <v-col v-for="(data, i) in result.forecast" :key="i">
              <div style="font-size: 1.2em;">
                {{ data.value }}
              </div>
            </v-col>
          </v-row>
          <v-row no-gutters class="mt-2">
            <v-col v-for="(data, i) in result.forecast" :key="i">
              <div style="font-size: 1.2em;">
                {{ data.time }}
              </div>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </div>
    <div
      v-else
      class="text-center"
      style="display: flex; 
          align-items: center; 
          justify-content: center;"
    >
           .
    </div>
  </div>
</template>
<script>
export default {
  async mounted() {
    await this.getWeather();
  },
  methods: {
    async getWeather() {
      try {
        var result = await this.$http.get("weather/");
        result = result.data;

        if (result.temp != 0) {
          this.result = result;
        }

        this.loading = true;
      } catch {
        this.loading = true;
      }
    },
    changeIcon(status) {
      switch (status) {
        case "":
          return "mdi-white-balance-sunny";
        case "  ":
          return "mdi-weather-partly-rainy";
        case "":
          return "mdi-apple-icloud";
        case " ":
          return "mdi-weather-rainy";
        case " /":
          return "mdi-weather-partly-snowy-rainy";
        case "":
          return "mdi-apple-icloud";
        case "  ":
          return "mdi-weather-rainy";
        case "":
          return "mdi-weather-pouring";
        case "":
          return "mdi-weather-snowy-rainy";
        case "":
          return "mdi-weather-snowy";
        default:
          return "mdi-white-balance-sunny";
      }
    },
    finedustTextColor(text) {
      switch (text) {
        case "":
          return "blue--text";
        case "":
          return "green--text";
        case "":
          return "orange--text";
        case "":
          return "red--text";
        default:
          return "black--text";
      }
    },
  },
  data() {
    return {
      loading: false,
      result: null,
    };
  },
};
</script>
