<template>
  <apexchart
    v-if="loading"
    type="bar"
    height="145vh"
    width="100%"
    :options="chartOptions"
    :series="series"
  >
  </apexchart>
</template>
<script>
export default {
  watch: {},
  async created() {},
  async mounted() {
    await this.getFallDetection();
  },
  methods: {
    async getFallDetection() {
      try {
        var result = await this.$http.post("events/fall_detect/all", {
          uid: this.$session.user.id,
        });
        this.series.push({ name: "Fall", data: result.data });
        this.loading = true;
      } catch {
        this.series.push({ name: "Fall", data: [] });
        this.loading = true;
      }
    },
  },
  data() {
    return {
      chartOptions: {
        chart: {
          type: "bar",
        },

        xaxis: {
          axisBorder: {
            show: false,
          },
          axisTicks: {
            show: false,
          },
          crosshairs: {
            fill: {
              type: "gradient",
              gradient: {
                colorFrom: "#D8E3F0",
                colorTo: "#BED1E6",
                stops: [0, 100],
                opacityFrom: 0.4,
                opacityTo: 0.5,
              },
            },
          },
          tooltip: {
            enabled: true,
          },
        },
        yaxis: {
          axisBorder: {
            show: false,
          },
          axisTicks: {
            show: false,
          },
        },
      },
      series: [],
      loading: false,
    };
  },
};
</script>
