<template>
  <div>
    <div class="text-end pt-10 mr-10">
      <span class=" display-1 green--text font-weight-bold">{{ bandnum }}</span>
      <span class=" display-1 grey--text font-weight-bold">/7</span>
    </div>
    <canvas class="mt-n10" id="canvas" width="400" height="400"></canvas>
  </div>
</template>
<script>
export default {
  props: ["band_data"],
  watch: {
    band_data() {
      var index = this.bandinfo
        .map(function(d) {
          return d["bid"];
        })
        .indexOf(this.band_data.extAddress.high);
      if (index !== -1) {
        switch (this.band_data.active) {
          case "false":
            this.bandinfo[index].bandvalue = this.bandvalue;
            break;
          case "true":
            this.bandinfo[index].bandvalue = this.band_data;
            break;
        }
      }
      this.draw();
    },
  },
  methods: {
    async getBandList() {
      try {
        var band = null;
        var bands = null;

        switch (this.$session.user.permission) {
          case 0:
            band = await this.$http.get("bands/list");
            this.restapi = true;
            if (band != null) bands = band.data.data;

            break;
          case 1:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            this.restapi = true;
            bands = band.data.data;
            break;
          case 2:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            this.restapi = true;
            bands = band.data.data;
            break;
        }
        if (bands != null) {
          for (var i = 0; i < bands.length; i++) {
            // console.log(bands[i].id);
            this.bandinfo.push({
              id: bands[i].id,
              bid: bands[i].bid,
              alias: bands[i].alias,
              name: bands[i].name,
              birth: bands[i].birth,
              created: bands[i].created,
              gender: bands[i].gender,
              connect_state: bands[i].connect_state,
              disconnect_time: bands[i].disconnect_time,
              connect_time: bands[i].connect_time,

              bandvalue: this.bandvalue,
            });
          }
        }
      } catch (ex) {
        console.log(ex);
      }
    },
    draw() {
      var width = 400;
      var height = 400;
      this.ctx.clearRect(0, 0, width, height);
      this.ctx.translate(width / 2, height / 2);
      this.ctx.font = "20px Arial";
      //   /* variables used for plotting*/
      var x, y, r, s, angle, i;

      /* draw central node -- panCoord
                  set up pan-coord parameters */
      x = 0;
      y = 0;
      s = 7;
      var rssi = 0;
      /* draw concentrator */

      /* select the starting angle for the first node to be 90 */
      angle = 90;
      /* calculate the difference in angle between two devices */
      var diff = 360 / this.bandnum;
      /* draw nodes */
      var num = 0;
      for (i = 0; i < this.bandinfo.length; i++) {
        if (this.bandinfo[i].bandvalue.active == "true") {
          angle = 90 + num * diff;

          rssi = this.bandinfo[i].bandvalue.rssi;
          /* set parameters */
          /* scale RSSI for short range */
          r = ((height - 80) / 2) * (rssi / -80) + s * 3;
          x = r * Math.cos(angle * (Math.PI / 180));
          y = r * Math.sin(angle * (Math.PI / 180));
          s = 7;

          /* Draw line to Concentrator (node 0) */

          this.ctx.beginPath();
          this.ctx.moveTo(0, 0);
          this.ctx.lineTo(x, y);
          this.ctx.strokeStyle = "gray";
          this.ctx.lineWidth = 6;
          this.ctx.stroke();
          /* render the node */

          this.ctx.beginPath();
          this.ctx.arc(x, y, 14, 2 * Math.PI, 0);
          this.ctx.fillStyle = "gray";
          this.ctx.fill();

          this.ctx.fillStyle = "White";

          x = x > 70 ? x - 12 : x - 6;
          y = y + 5;
          this.ctx.fillText(this.bandinfo[i].id, x, y);
          num++;
        }
      }
      this.bandnum = num;
      this.ctx.beginPath();
      this.ctx.arc(0, 0, 20, 0, 2 * Math.PI);
      this.ctx.fillStyle = "Green";
      this.ctx.fill();
      this.ctx.fillStyle = "White";
      this.ctx.fillText("GW", -18, 6);
      /* set canvas x/y back to 0,0 */
      this.ctx.translate(-(width / 2), -(height / 2));
    },
  },
  async mounted() {
    var c = document.getElementById("canvas");
    this.ctx = c.getContext("2d");
    if (this.$session.user != null) {
      await this.getBandList();
    }

    this.draw();
  },

  data() {
    return {
      bandinfo: [],
      ctx: null,
      bandvalue: {
        bandData: {
          start_byte: "",
          sample_count: "",
          fall_detect: "",
          battery_level: "",
          hrConfidence: "",
          spo2Confidence: "",
          hr: "",
          spo2: "",
          motionFlag: "",
          scdState: "",
          activity: "",
          walk_steps: "",
          run_steps: "",
          x: "",
          y: "",
          z: "",
          t: "",
          h: "",
        },
        rssi: "",
        extAddress: {
          row: 0,
        },
        active: "false",
      },
      bandnum: 0,
    };
  },
};
</script>
