<template>
  <div class="mt-4">
    <v-data-iterator
      :items="gatewayinfo"
      :items-per-page.sync="itemsPerPage"
      :page.sync="page"
      :search="search"
      :no-data-text="nodata"
      hide-default-footer
    >
      <template v-slot:header>
        <v-toolbar flat class="primary">
          <v-text-field
            v-model="search"
            outlined
            label="Search"
            color="point2"
            append-icon="mdi-magnify"
          ></v-text-field>
        </v-toolbar>
      </template>

      <template v-slot:default="props">
        <v-container fluid class="mt-n2">
          <v-list class="overflow-y-auto primary" height="calc(100vh - 220px)">
            <v-card
              v-for="(gateway, index) in props.items"
              :key="index"
              flat
              class="mb-6 thirdly"
            >
              <v-list-item
                action
                :key="gateway.alias"
                @click="
                  $router.push({
                    path: 'gateway/detail',
                    query: { gatewayinfo: JSON.stringify(gateway) },
                  })
                "
              >
                <v-list-item-icon class="list-item">
                  <v-icon
                    small
                    :color="gateway.connect_state === true ? 'yellow' : 'grey'"
                    >mdi-circle</v-icon
                  >
                </v-list-item-icon>

                <v-list-item-content class="list-item">
                  <v-list-item-title>{{ gateway.id }}</v-list-item-title>
                </v-list-item-content>
                <v-list-item-content class="list-item">
                  <v-list-item-title>{{ gateway.pid }}</v-list-item-title>
                </v-list-item-content>
                <v-list-item-content class="list-item">
                  <v-list-item-title>{{ gateway.alias }}</v-list-item-title>
                </v-list-item-content>
                <v-list-item-content class="list-item">
                  <v-list-item-title
                    >{{ gateway.cbandnum }}/{{
                      gateway.bandnum
                    }}</v-list-item-title
                  >
                </v-list-item-content>
                <v-list-item-content class="list-item ">
                  <v-list-item-title>{{
                    $date.changeFullDateFormat(
                      new Date(gateway.disconnect_time) - $date.KR_TIME_DIFF
                    )
                  }}</v-list-item-title>
                </v-list-item-content>
                <v-list-item-content class="list-item ">
                  <v-list-item-title>{{
                    $date.changeFullDateFormat(
                      new Date(gateway.connect_time) - $date.KR_TIME_DIFF
                    )
                  }}</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-list>
        </v-container>
      </template>
      <template v-slot:footer>
        <v-row class="" align="center" justify="center">
          <v-spacer></v-spacer>

          <span
            class="mr-4
            grey--text"
          >
            Page {{ page }} of {{ numberOfPages }}
          </span>

          <v-icon class="mr-1" @click="formerPage">mdi-chevron-left</v-icon>

          <v-icon class="ml-1 mr-4" @click="nextPage">mdi-chevron-right</v-icon>
        </v-row>
      </template>
    </v-data-iterator>
  </div>
</template>
<script>
export default {
  props: ["gatewayinfo"],
  watch: {},
  mounted() {},
  data: () => ({
    search: "",
    nodata: "",
    KR_TIME_DIFF: 9 * 60 * 60 * 1000,
    itemsPerPageArray: [5, 10, 15, 20],
    page: 1,
    itemsPerPage: 10,
  }),
  methods: {
    // changeJson() {
    //   this.user = JSON.parse(this.userInfo);
    // },
    changeDateFormat(date) {
      var current_datetime = new Date(date);
      return (
        current_datetime.getFullYear() +
        "-" +
        (current_datetime.getMonth() + 1) +
        "-" +
        current_datetime.getDate() +
        " " +
        current_datetime.getHours() +
        ":" +
        current_datetime.getMinutes()
      );
    },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    async getBandList() {
      try {
        var band = null;
        var bands = null;
        var group = await this.$http.get(
          "users/groupinfo/" + this.$session.user.id
        );
        group = group.data.data;
        switch (group[0].permission) {
          case 0:
            band = await this.$http.get("bands/list");
            bands = band.data.data;
            break;
          case 1:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
          case 2:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
        }
        if (bands != null) {
          for (var i = 0; i < bands.length; i++) {
            this.bandinfo.push({
              alias: bands[i].alias,
              bid: bands[i].bid,
              birth: bands[i].birth,
              created: bands[i].created,
              gender: bands[i].gender,
              id: bands[i].id,
              name: bands[i].name,
              bandvalue: this.bandvalue,
            });
          }
        }
      } catch (ex) {
        console.log(ex);
      }
    },
    onDataHandler(data) {
      //console.log(data);
      if (data.active === "false") {
        this.bandinfo[data.shortAddress - 1].bandvalue = this.bandvalue;
      } else {
        this.bandinfo[data.shortAddress - 1].bandvalue = data;
      }
    },

    async listClick(bandinfo) {
      //console.log(bandinfo);
      if (await this.$session.historyCheck()) {
        this.$router.replace({
          name: "Band Info",
          query: { bandInfo: JSON.stringify(bandinfo) },
        });
      } else {
        this.$session.releaseSession();
        this.$router.replace("/");
      }
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.gatewayinfo.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
};
</script>
<style lang="scss">
.list-item {
  align-self: center; // [jk]
}
.text-style {
  font-size: 1.2rem;
}
.align-style {
  width: 40px;
}
</style>
