<template>
  <div class="mt-4">
    <v-data-iterator
      :items="bandinfo"
      :search="search"
      :no-data-text="nodata"
      :page.sync="page"
      :items-per-page.sync="itemsPerPage"
      hide-default-footer
    >
      <template v-slot:header>
        <v-toolbar flat class="primary">
          <v-text-field
            v-model="search"
            outlined
            label="Search"
            color="point2"
            append-icon="mdi-magnify"
          ></v-text-field>
        </v-toolbar>
      </template>
      <template v-slot:default="props">
        <v-container fluid class="mt-n2">
          <v-row>
            <v-col v-for="(band, n) in props.items" :key="n" cols="2">
              <v-card class="rounded-lg" @click="$emit('moveBandDatail', band)">
                <v-list
                  justify="center"
                  height="140"
                  :color="band.bandvalue.active === 'true' ? 'thirdly' : 'grey'"
                  ><v-list-item
                    ><v-list-item-content class="mt-n6 "
                      ><v-list-item-title class="title">{{
                        band.id
                      }}</v-list-item-title></v-list-item-content
                    >
                    <v-list-item-icon class="list-item  mt-n2">
                      <v-img
                        v-if="
                          band.bandvalue.bandData.scdState === 3 ||
                            band.bandvalue.bandData.scdState === 2 ||
                            band.bandvalue.active === 'false'
                        "
                        :src="require('../../assets/image/scdState.svg')"
                        width="28"
                      >
                      </v-img>
                      <v-img
                        v-else
                        :src="require('../../assets/image/noScdState.svg')"
                        width="28"
                      >
                      </v-img>
                      <v-icon v-if="band.bandvalue.active === 'false'">
                        mdi-signal-cellular-outline
                      </v-icon>
                      <v-icon
                        v-else-if="band.bandvalue.rssi > -30"
                        color="black"
                      >
                        mdi-signal-cellular-3
                      </v-icon>
                      <v-icon
                        v-else-if="band.bandvalue.rssi > -50"
                        color="black"
                      >
                        mdi-signal-cellular-2
                      </v-icon>
                      <v-icon
                        v-else-if="band.bandvalue.rssi > -75"
                        color="black"
                      >
                        mdi-signal-cellular-1
                      </v-icon>
                      <v-icon v-else color="black">
                        mdi-signal-cellular-outline
                      </v-icon>

                      <v-icon v-if="band.bandvalue.active === 'false'">
                        mdi-battery-outline
                      </v-icon>
                      <v-icon
                        v-else-if="band.bandvalue.bandData.battery_level > 80"
                        color="black"
                      >
                        mdi-battery-high
                      </v-icon>
                      <v-icon
                        v-else-if="band.bandvalue.bandData.battery_level > 40"
                        color="black"
                      >
                        mdi-battery-medium
                      </v-icon>
                      <v-icon
                        v-else-if="band.bandvalue.bandData.battery_level > 15"
                        color="black"
                      >
                        mdi-battery-low
                      </v-icon>
                      <v-icon v-else color="black">
                        mdi-battery-outline
                      </v-icon>
                    </v-list-item-icon>
                  </v-list-item>
                  <div class="text-center">{{ band.bid }}</div>
                </v-list>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </template>
      <template v-slot:footer>
        <v-row class="" align="center" justify="center">
          <v-spacer></v-spacer>

          <span
            class="mr-4
            grey--text"
          >
            Page {{ page }} of {{ numberOfPages }}
          </span>

          <v-icon class="mr-1" @click="formerPage">mdi-chevron-left</v-icon>

          <v-icon class="ml-1 mr-4" @click="nextPage">mdi-chevron-right</v-icon>
        </v-row>
      </template>
    </v-data-iterator>
  </div>
</template>

<script>
export default {
  props: ["bandinfo"],
  data: () => ({
    restapi: false,
    num: 75,
    search: "",
    nodata: "",
    page: 1,
    itemsPerPage: 24,
  }),
  methods: {
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.bandinfo.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
};
</script>
<style lang="scss">
.hidden {
  visibility: hidden;
}

