<template>
  <div>
    <v-toolbar flat class="primary">
      <v-toolbar-title class="text-uppercase font-weight-bold display-1">
         
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-list class="primary">
        <v-list-item>
          <v-list-item-content style="text-align: right">
            <v-list-item-title class="text-h6">
              {{ userinfo.name }}
            </v-list-item-title>
            <v-list-item-subtitle
              v-text="userinfo.permission === 0 ? 'admin' : 'staff'"
            ></v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-toolbar>
    <div class="mx-4 mt-3">
      <v-divider></v-divider>
      <div class="mt-4">
        <v-row>
          <v-col cols="4">
            <v-card>
              <v-toolbar flat>
                <v-card-subtitle
                  class="black--text font-weight-bold mt-n2 ml-n4"
                >
                  
                </v-card-subtitle>
                <v-spacer></v-spacer>
                <v-btn
                  class="mt-n2 font-weight-medium"
                  text
                  color="point2"
                  depressed
                  ></v-btn
                >
              </v-toolbar>

              <v-list class="mt-n4">
                <v-list-item>
                  <v-list-item-content>
                    
                  </v-list-item-content>
                  <v-list-item-action>
                    {{ userinfo.name }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    UID
                  </v-list-item-content>
                  <v-list-item-action>
                    {{ userinfo.uid }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                     
                  </v-list-item-content>
                  <v-list-item-action>
                    {{ userinfo.username }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    
                  </v-list-item-content>
                  <v-list-item-action>
                    {{ groupinfo.groupname }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    
                  </v-list-item-content>
                  <v-list-item-action>
                    {{ $date.changeLocalDateFormat(userinfo.created) }}
                  </v-list-item-action>
                </v-list-item>
              </v-list></v-card
            >
          </v-col>
          <v-col cols="8">
            <v-card>
              <v-toolbar class="mb-n4" flat>
                <v-toolbar-title class="subtitle-1 black--text font-weight-bold"
                  ></v-toolbar-title
                >

                <v-spacer></v-spacer>
                <v-btn depressed color="white">
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </v-toolbar>
              <v-data-table
                falt
                :headers="headers"
                :items="bandList"
                :items-per-page="3"
              >
                <template v-slot:item.actions="{ item }">
                  <v-icon small @click="deleteItem(item)">
                    mdi-delete
                  </v-icon>
                </template>
              </v-data-table>
            </v-card>
          </v-col>
        </v-row>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  async mounted() {
    this.userinfo = JSON.parse(this.$route.query.user);
    this.groupinfo = JSON.parse(this.$route.query.group);
    await this.getBandList();
  },
  methods: {
    async getBandList() {
      var bandlist = null;
      try {
        bandlist = await this.$http.get(
          "users/bandinfo/" + this.$session.user.id
        );
        this.bandList = bandlist.data.data;
        //this.bandList = bandlist.data.data;
      } catch (ex) {
        this.bandList = [];
      }
    },
  },
  data() {
    return {
      userinfo: { name: "", uid: "", username: "", created: "" },
      groupinfo: { groupname: "" },
      headers: [
        {
          text: "",
          align: "start",
          sortable: true,
          value: "id",
        },
        { text: " ", value: "bid", sortable: true },
        { text: "", value: "alias", sortable: true },
        { text: "", value: "name", sortable: true },
        { text: "", value: "actions", sortable: false },
      ],
      bandList: [
        { id: 1, bid: "0x00", alias: "D1", name: "" },
        { id: 2, bid: "0x00", alias: "D1", name: "" },
        { id: 3, bid: "0x00", alias: "D1", name: "" },
      ],
    };
  },
};
</script>
