<template>
  <div class="mt-4">
    <v-data-iterator
      :items="bandinfo"
      :items-per-page.sync="itemsPerPage"
      :page.sync="page"
      :search="search"
      :no-data-text="nodata"
      hide-default-footer
    >
      <template v-slot:header>
        <v-toolbar flat class="primary">
          <v-text-field
            v-model="search"
            outlined
            label="Search"
            color="point2"
            append-icon="mdi-magnify"
          ></v-text-field>
        </v-toolbar>
      </template>
      <template v-slot:default="props">
        <v-container fluid class="mt-n2">
          <v-list class="overflow-y-auto primary" height="calc(100vh - 220px)">
            <template v-for="(band, index) in props.items">
              <v-card :key="index" flat class="mb-6 thirdly">
                <v-list-item action @click="$emit('moveBandDatail', band)">
                  <v-list-item-icon class="list-item">
                    <v-icon
                      small
                      :color="
                        band.bandvalue.active === 'true' ? 'yellow' : 'grey'
                      "
                      >mdi-circle</v-icon
                    >
                  </v-list-item-icon>

                  <v-list-item-content class="list-item">
                    <v-list-item-title>{{ band.id }}</v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-content class="list-item">
                    <v-list-item-title>{{ band.bid }}</v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-content class="list-item">
                    <v-list-item-title>{{ band.alias }}</v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-content class="list-item">
                    <v-list-item-title>{{ band.name }}</v-list-item-title>
                  </v-list-item-content>

                  <v-list-item-icon class="list-item">
                    <v-img
                      v-if="band.bandvalue.active === 'false'"
                      :src="require('../../assets/image/falseScdState.svg')"
                      width="28"
                    >
                    </v-img>
                    <v-img
                      v-else-if="
                        band.bandvalue.bandData.scdState === 3 ||
                          band.bandvalue.bandData.scdState === 2 ||
                          band.bandvalue.active === 'false'
                      "
                      :src="require('../../assets/image/scdState.svg')"
                      width="28"
                    >
                    </v-img>
                    <v-img
                      v-else
                      :src="require('../../assets/image/noScdState.svg')"
                      width="28"
                    >
                    </v-img>
                    <v-icon
                      v-if="band.bandvalue.active === 'false'"
                      color="grey"
                    >
                      mdi-signal-cellular-outline
                    </v-icon>
                    <v-icon v-else-if="band.bandvalue.rssi > -30" color="black">
                      mdi-signal-cellular-3
                    </v-icon>
                    <v-icon v-else-if="band.bandvalue.rssi > -50" color="black">
                      mdi-signal-cellular-2
                    </v-icon>
                    <v-icon v-else-if="band.bandvalue.rssi > -75" color="black">
                      mdi-signal-cellular-1
                    </v-icon>
                    <v-icon v-else color="black">
                      mdi-signal-cellular-outline
                    </v-icon>

                    <v-icon
                      v-if="band.bandvalue.active === 'false'"
                      color="grey"
                    >
                      mdi-battery-high
                    </v-icon>
                    <v-icon
                      v-else-if="band.bandvalue.bandData.battery_level > 80"
                      color="black"
                    >
                      mdi-battery-high
                    </v-icon>
                    <v-icon
                      v-else-if="band.bandvalue.bandData.battery_level > 40"
                      color="black"
                    >
                      mdi-battery-medium
                    </v-icon>
                    <v-icon
                      v-else-if="band.bandvalue.bandData.battery_level > 15"
                      color="black"
                    >
                      mdi-battery-low
                    </v-icon>
                    <v-icon v-else color="black">
                      mdi-battery-outline
                    </v-icon>

                    <div
                      :class="
                        band.bandvalue.active === 'false'
                          ? 'align-style-false'
                          : 'align-style-true'
                      "
                    >
                      {{ band.bandvalue.bandData.battery_level }}%
                    </div>
                  </v-list-item-icon>

                  <!-- <v-list-item-icon class="list-item">
                <v-btn
                  depressed
                  block
                  class="primary"
                  @click="log(bands[index])"
                  ></v-btn
                >
              </v-list-item-icon> -->
                </v-list-item>
              </v-card>
            </template>
          </v-list>
        </v-container>
      </template>
      <template v-slot:footer>
        <v-row class="" align="center" justify="center">
          <v-spacer></v-spacer>

          <span
            class="mr-4
            grey--text"
          >
            Page {{ page }} of {{ numberOfPages }}
          </span>

          <v-icon class="mr-1" @click="formerPage">mdi-chevron-left</v-icon>

          <v-icon class="ml-1 mr-4" @click="nextPage">mdi-chevron-right</v-icon>
        </v-row>
      </template>
    </v-data-iterator>
  </div>
</template>
<script>
export default {
  props: ["bandinfo"],
  data: () => ({
    restapi: false,
    search: "",
    nodata: "",
    bandvalue: {
      bandData: {
        start_byte: "",
        sample_count: "",
        fall_detect: "",
        battery_level: "",
        hrConfidence: "",
        spo2Confidence: "",
        hr: "",
        spo2: "",
        motionFlag: "",
        scdState: "",
        activity: "",
        walk_steps: "",
        run_steps: "",
        x: "",
        y: "",
        z: "",
        t: "",
        h: "",
      },
      rssi: -80,
      extAddress: {
        row: 0,
      },
      active: "true",
    },
    itemsPerPageArray: [5, 10, 15, 20],
    page: 1,
    itemsPerPage: 10,
  }),
  methods: {
    // changeJson() {
    //   this.user = JSON.parse(this.userInfo);
    // },
    moveBandDetail(band) {
      this.$router.replace({
        path: "band/detail",
        query: {
          bandinfo: JSON.stringify({
            id: band.id,
            bid: band.bid,
            alias: band.alias,
            name: band.name,
            birth: band.birth,
            gender: band.gender,
            created: band.created,
            connect_state: band.connect_state,
            disconnect_time: band.disconnect_time,
            connect_time: band.connect_time,
          }),
        },
      });
    },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    async getBandList() {
      try {
        var band = null;
        var bands = null;
        var group = await this.$http.get(
          "users/groupinfo/" + this.$session.user.id
        );
        group = group.data.data;
        // console.log(group[0].permission);
        switch (group[0].permission) {
          case 0:
            band = await this.rest.bands_list();
            if (band != null) {
              bands = band.data.data;
            }

            break;
          case 1:
            // console.log(this.$session.user.id);
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
          case 2:
            // console.log(this.$session.user.id);
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
        }
        if (bands != null) {
          for (var i = 0; i < bands.length; i++) {
            // console.log(bands[i].id);
            this.bandinfo.push({
              alias: bands[i].alias,
              bid: bands[i].bid,
              birth: bands[i].birth,
              created: bands[i].created,
              gender: bands[i].gender,
              id: bands[i].id,
              name: bands[i].name,
              bandvalue: this.bandvalue,
            });
            // console.log(bands[i]);
          }
        }
      } catch (ex) {
        console.log(ex);
      }
    },

    async listClick(bandinfo) {
      // console.log(bandinfo);
      //console.log(bandinfo);
      if (await this.$session.historyCheck()) {
        this.$router.replace({
          name: "Band Info",
          query: { bandInfo: JSON.stringify(bandinfo) },
        });
      } else {
        this.$session.releaseSession();
        this.$router.replace("/");
      }
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.bandinfo.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
};
</script>
<style lang="scss">
.list-item {
  align-self: center; // [jk]
}

.text-style {
  font-size: 1.2rem;
}
.align-style-true {
  width: 40px;
  font-weight: bold;
  color: black;
}
.align-style-false {
  width: 40px;
  font-weight: bold;
  color: grey;
}
</style>
