<template>
  <v-app>
    <drawer v-if="$session.user != null" />
    <v-main class="primary">
      <Alert
        :dialogCheck="alertView"
        @dialog-event="dialogEvent"
        :value="value"
      />
      <router-view
        :band_data="band_data"
        :gateway_data="gateway_data"
        v-if="$session.user != null"
        class="router-view "
      />
      <login v-else />
    </v-main>
  </v-app>
</template>

<script>
import drawer from "./components/Drawer.vue";
import login from "./components/Login.vue";
import Alert from "./components/Alert.vue";
export default {
  components: { drawer, login, Alert },
  async mounted() {
    this.$session.$on("data", (data) => {
      if (this.$session.user != null) {
        this.band_data = data;
      }
    });
    this.$session.$on("gateway_connect", (data) => {
      if (this.$session.user != null) {
        var index = this.gateway_data
          .map(function(g) {
            return g["panid"];
          })
          .indexOf(data.panid);
        if (index === -1) {
          this.gateway_data.push(data);
        } else {
          this.gateway_data[index] = data;
        }
      }
    });
    //  this.$session.$on("gateway_disconnect", (data) => {
    //   if (this.$session.user != null) {
    //     this.gateway_data = data;
    //   }
    // });
    this.$session.$on("efwbasync", this.onAlertCheck.bind(this));
  },
  data: () => ({
    viewData: {
      value: true,
    },
    alertView: false,
    value: {
      title: "",
      subtitle: "",
      content: "",
      state: 0,
    },
    windowHeight: null,
    band_data: null,
    gateway_data: [],
    bandlist: [],
  }),
  methods: {
    onAlertCheck(data) {
      this.makeMessage(data);
    },
    makeMessage(data) {
      this.value.subtitle = data.bid;
      switch (data.type) {
        case 0: {
          this.value.title = " ";
          this.value.content = 0;
          break;
        }
        case 1: {
          this.value.title = " ";
          this.value.content = 5000;
          break;
        }
        case 2: {
          this.value.title = "   ";
          this.value.content = 5000;
          break;
        }
        case 3: {
          this.value.title = " ";
          this.value.content = 5000;
          break;
        }
        case 4: {
          this.value.title = " ";
          this.value.content = 0;
          break;
        }
      }
      this.$toast.error(this.value.subtitle + " " + this.value.title, {
        position: "top-right",
        timeout: this.value.content,
        closeOnClick: true,
        pauseOnFocusLoss: true,
        pauseOnHover: true,
        draggable: true,
        draggablePercent: 0.6,
        showCloseButtonOnHover: false,
        hideProgressBar: true,
        closeButton: "button",
        icon: true,
        rtl: false,
      });
    },
    // onAlertCheck(data) {
    //   this.bands[data.shortAddress - 1].bandvalue = data;
    //   if (this.$router.history.current.name != "Login") {
    //     var flag = false;
    //     for (var i = 0; i < this.bands.length; i++) {
    //       if (this.bands[i].bandvalue.bandData.fall_detect === 1) {
    //         flag = true;
    //         this.value.subtitle =
    //           "0x0" +
    //           this.bands[i].bandinfo.bid +
    //           " " +
    //           this.bands[i].bandinfo.name;
    //         this.alertView = true;
    //       }
    //     }
    //     if (!flag) {
    //       this.alertView = false;
    //     }
    //   }
    // },
    dialogEvent(check) {
      this.alertView = check;
    },
  },
};
</script>
