<template>
  <div>
    <v-toolbar flat class="primary"> </v-toolbar>
    <div class="mt-2">
      <v-data-iterator
        :items="userlist"
        :items-per-page.sync="itemsPerPage"
        :page.sync="page"
        :search="search"
        :no-data-text="nodata"
        hide-default-footer
      >
        <template v-slot:header>
          <v-toolbar flat class="primary">
            <v-text-field
              v-model="search"
              outlined
              label="Search"
              color="point2"
              append-icon="mdi-magnify"
            ></v-text-field>
          </v-toolbar>
        </template>

        <template v-slot:default="props">
          <v-container fluid>
            <v-list
              class="overflow-y-auto primary"
              height="calc(100vh - 250px)"
            >
              <template v-for="(user, index) in props.items">
                <v-card :key="index" flat class="mb-6 thirdly">
                  <v-list-item action :key="user.id" @click="listClick(user)">
                    <v-list-item-icon class="list-item">
                      <v-icon small :color="true === 'true' ? 'yellow' : 'grey'"
                        >mdi-circle</v-icon
                      >
                    </v-list-item-icon>

                    <v-list-item-content class="list-item ">
                      <v-list-item-title>{{ user.id }}</v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-content class="list-item">
                      <v-list-item-title>{{ user.uid }}</v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-content class="list-item">
                      <v-list-item-title>{{ user.name }}</v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-content class="list-item">
                      <v-list-item-title
                        v-text="user.permission === 0 ? 'admin' : 'staff'"
                      ></v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-content class="list-item">
                      <v-list-item-title>{{
                        group.groupname
                      }}</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-card>
              </template>
            </v-list>
          </v-container>
        </template>
        <template v-slot:footer>
          <v-row class="" align="center" justify="center">
            <v-spacer></v-spacer>

            <span
              class="mr-4
            grey--text"
            >
              Page {{ page }} of {{ numberOfPages }}
            </span>

            <v-icon class="mr-1" @click="formerPage">mdi-chevron-left</v-icon>

            <v-icon class="ml-1 mr-4" @click="nextPage"
              >mdi-chevron-right</v-icon
            >
          </v-row>
        </template>
      </v-data-iterator>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  mounted() {
    this.getUserList();
  },
  data: () => ({
    bandinfo: [
      {
        bandvalue: {
          bandData: {
            start_byte: "",
            sample_count: "",
            fall_detect: "",
            battery_level: "",
            hrConfidence: "",
            spo2Confidence: "",
            hr: "",
            spo2: "",
            motionFlag: "",
            scdState: "",
            activity: "",
            walk_steps: "",
            run_steps: "",
            x: "",
            y: "",
            z: "",
            t: "",
            h: "",
          },
          rssi: -80,
          extAddress: {
            row: 0,
          },
          active: "true",
        },
      },
      {
        bandvalue: {
          bandData: {
            start_byte: "",
            sample_count: "",
            fall_detect: "",
            battery_level: "",
            hrConfidence: "",
            spo2Confidence: "",
            hr: "",
            spo2: "",
            motionFlag: "",
            scdState: "",
            activity: "",
            walk_steps: "",
            run_steps: "",
            x: "",
            y: "",
            z: "",
            t: "",
            h: "",
          },
          rssi: -80,
          extAddress: {
            row: 0,
          },
          active: "false",
        },
      },
    ],
    userlist: [],
    search: "",
    nodata: "",
    bandvalue: {
      bandData: {
        start_byte: "",
        sample_count: "",
        fall_detect: "",
        battery_level: "",
        hrConfidence: "",
        spo2Confidence: "",
        hr: "",
        spo2: "",
        motionFlag: "",
        scdState: "",
        activity: "",
        walk_steps: "",
        run_steps: "",
        x: "",
        y: "",
        z: "",
        t: "",
        h: "",
      },
      rssi: -80,
      extAddress: {
        row: 0,
      },
      active: "true",
    },
    itemsPerPageArray: [5, 10, 15, 20],
    page: 1,
    itemsPerPage: 10,
    detail: false,
    group: { groupname: "" },
  }),
  methods: {
    async getUserList() {
      try {
        var res = await this.$http.get(
          "users/samegroup/" + this.$session.user.id
        );
        var groupinfo = await this.$http.post("groups/detail", {
          gid: res.data.data.gid,
        });
        this.group = groupinfo.data.groups;
        this.userlist = res.data.data.userlist;
      } catch (ex) {
        this.userlist = [];
      }
    },
    // changeJson() {
    //   this.user = JSON.parse(this.userInfo);
    // },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    async getBandList() {
      try {
        var band = null;
        var bands = null;
        var group = await this.$http.get(
          "users/groupinfo/" + this.$session.user.id
        );
        group = group.data.data;
        switch (group[0].permission) {
          case 0:
            band = await this.$http.get("bands/list");
            bands = band.data.data;
            break;
          case 1:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
          case 2:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
        }
        if (bands != null) {
          for (var i = 0; i < bands.length; i++) {
            this.bandinfo.push({
              alias: bands[i].alias,
              bid: bands[i].bid,
              birth: bands[i].birth,
              created: bands[i].created,
              gender: bands[i].gender,
              id: bands[i].id,
              name: bands[i].name,
              bandvalue: this.bandvalue,
            });
          }
        }
      } catch (ex) {
        console.log(ex);
      }
    },
    onDataHandler(data) {
      //console.log(data);
      if (data.active === "false") {
        this.bandinfo[data.shortAddress - 1].bandvalue = this.bandvalue;
      } else {
        this.bandinfo[data.shortAddress - 1].bandvalue = data;
      }
    },

    listClick(user) {
      this.$router.replace({
        path: "/user/detail",
        query: {
          user: JSON.stringify(user),
          group: JSON.stringify(this.group),
        },
      });
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(5 / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
};
</script>
